
.v-sel-c--fw {
  width: 100%;
  display: inline-block;
  position: relative;
  border: 3px solid #091f2f;
  border-radius: 0;
  height: calc(2 * 1rem + 2 * 3px + 1.5 * 1rem);
}
.v-sel-c--fw .v-select {
    background-color: #fff;
    display: inline-block;
    font-family: Lora,Georgia,serif;
    font-size: 16px;
    font-size: 1rem;
    line-height: 2;
    padding: 0 85px 0 16px;
    height: 100%;
    margin: 0;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    width: 100%;
    position: default;
}
.v-sel-c--fw .v-select .dropdown-toggle {
      border: 0;
      height: 100%;
}
.v-sel-c--fw .v-select .selected-tag {
      line-height: 3;
      height: 100%;
}
.v-sel-c--fw .v-select .open-indicator {
      position: absolute;
      z-index: 1;
      pointer-events: none;
      top: 0;
      bottom: 0;
      right: 0;
      border-left: 3px solid #091f2f;
      background: #288be4;
      width: 60px;
      height: 100%;
}
.v-sel-c--fw .v-select .open-indicator:before {
        background-image: url(//patterns.boston.gov/images/global/icons/chevron-down.svg);
        background-repeat: no-repeat;
        background-position: 50%;
        background-size: 20px;
        border: 0;
        width: 100%;
        height: 100%;
        -webkit-transform: none;
                transform: none;
}
.v-sel-c--fw .v-select.open .open-indicator:before {
      -webkit-transform: rotate(-180deg);
              transform: rotate(-180deg);
}
.v-sel-c--fw .v-select input[type=search], .v-sel-c--fw .v-select input[type=search]:focus {
      font-family: Lora,Georgia,serif;
      height: 100%;
}
.v-sel-c--fw .v-select input[type=search]::-webkit-input-placeholder {
      font-style: italic;
      color: #828282;
      line-height: 3.5;
}
.v-sel-c--fw .v-select input[type=search]:-ms-input-placeholder {
      font-style: italic;
      color: #828282;
      line-height: 3.5;
}
.v-sel-c--fw .v-select input[type=search]::-ms-input-placeholder {
      font-style: italic;
      color: #828282;
      line-height: 3.5;
}
.v-sel-c--fw .v-select input[type=search]::placeholder {
      font-style: italic;
      color: #828282;
      line-height: 3.5;
}



